import React, { Component } from 'react';
import { connect } from 'react-redux'
import {addTask, editTask, toggleTask, removeTask} from '../../actions'
import { Divider } from 'semantic-ui-react'

import ButtonControl from '../../components/ButtonControl'
import TaskCard from '../../components/TaskCard'
import TaskModal from '../../components/TaskModal'

import 'semantic-ui-css/semantic.min.css'
import './App.css';

class App extends Component {

    state = { open: false };

    add = (id, title, description) => {
        this.props.addTask(title, description)
    };

    show = () => this.setState({ open: true });
    close = () => this.setState({ open: false });

    render() {
        return (
            <div className="app">
                <h1>TASK LIST EXAMPLE</h1>
                <h3>ReactJS, Redux, Semantic UI React</h3>
                <ButtonControl onClick={ this.show }/>
                <Divider />
                <TaskCard { ...this.props } />
                <TaskModal
                    dimmer='blurring'
                    size='tiny'
                    open={this.state.open}
                    onClose={this.close}
                    onClick={this.add}/>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return state
};

const mapDispatchToProps = dispatch => {
    return {
        addTask : (id, title) => dispatch(addTask(id, title)),
        toggleTask: id => dispatch(toggleTask(id)),
        removeTask: id => dispatch(removeTask(id)),
        editTask: (id, title, description) => dispatch(editTask(id, title, description))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(App)




